@import "../../scss/colors";


.sort-modal {
  &-window {
    position: relative;
    z-index: 20;
    max-width: 400px;
    background-color: $forcards;
    padding: 30px;
    margin: 50px auto;
    border-radius: 5px;
    display: flex;
    flex-direction: column;

    &__close {
      text-align: end;
      padding-bottom: 15px;

      img {
        cursor: pointer;
        height: 16px;
        width: 16px;
      }
    }

    &__title {
      color: #fff;
      text-transform: uppercase;
      font-weight: 600;
      font-size: 18px;
      text-align: center;
      padding-bottom: 50px;
    }

    &__list {
      display: flex;
      justify-content: space-around;
      padding-bottom: 40px;

      &__item {
        color: #fff;
        font-size: 18px;
        font-weight: 500;
        cursor: pointer;
        user-select: none;
        display: flex;
        align-items: center;
        position: relative;

        &.active {
          &::before {
            content: '';
            position: absolute;
            background: $purpleGrad;
            width: 140%;
            height: 4px;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
            bottom: -20px;
            right: 50%;
            transform: translateX(50%);
          }
        }

        img {
          margin-right: 15px;
        }
      }
    }

    &__sort-list {
      &_sort {
        display: none;

        &.active {
          display: block;
          min-height: 250px;
        }
      }

      &_category {
        overflow: auto;
        max-height: 250px;
        display: none;

        &.active {
          display: block;
        }

        &::-webkit-scrollbar {
          width: 4px;
        }

        &::-webkit-scrollbar-thumb {
          background-color: #414348;
          border-radius: 4px;
        }
      }

      &__item {
        padding: 10px 0;

        span {
          color: #ffff;
          font-size: 18px;
          font-weight: 500;
          display: flex;
          align-items: center;
          cursor: pointer;

          &:before {
            content: '';
            display: inline-block;
            margin-right: 10px;
            background-repeat: no-repeat;
            background-image: url("../../assets/images/icon_sort_disable.svg");
            height: 30px;
            width: 30px;
          }

          &.active {
            &:before {
              background-image: url("../../assets/images/icon_sort_active.svg");
            }
          }
        }
      }
    }

    &__button {
      border: none;
      padding: 15px 40px;
      font-size: 16px;
      font-weight: 600;
      border-radius: 23px;
      background: $purpleGrad;
      color: #fff;
      align-self: center;
      cursor: pointer;
      margin-top: 20px;
    }
  }
}