@import "../../scss/colors";
@import "../../scss/fonts";

.our-mission {
  &-title {
    font-weight: bold;
    color: #fff;
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 32px;
  }
  &__tiles {
    margin-bottom: 32px;
    &-grid-container {
      display: grid;
      grid-template-columns: repeat(3, 31.3333%);
      grid-template-rows: repeat(5, 160px);
      grid-row-gap: 32px;
      grid-column-gap: 32px;
    }
    &-player {
      border-radius: 23px;
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 4;
    }
    &-downloadApp {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 2;
      background: $purpleGrad;
      border-radius: 23px;
      padding: 11px 15px;
      color: #fff;
      &-content {
        display: flex;
        justify-content: space-between;
      }
      &-title {
        font-size: 18px;
        line-height: 27px;
        font-weight: bold;
      }
      &-tagline {
        font-size: 12px;
        line-height: 18px;
        color: #fafafa;
      }
      &-link {
        text-decoration: none;
        color: $purpleGrad;
        font-size: 13px;
        height: fit-content;
        line-height: 20px;
        border-radius: 17px;
        padding: 8px 14px;
        background-color: #fff;
      }
      &-images {
        display: flex;
        justify-content: space-between;
      }
      &-rocket {
        width: 104px;
        height: 104px;
        background-image: url("../../assets/images/rocket.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
      &-coin {
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        &-group {
          display: flex;
        }
        &.p1 {
          width: 45px;
          height: 45px;
          background-image: url("../../assets/images/coin-down.png");
        }
        &.p2 {
          width: 38px;
          height: 38px;
          align-self: center;
          background-image: url("../../assets/images/coin-up.png");
        }
        &.p3 {
          width: 38px;
          height: 38px;
          background-image: url("../../assets/images/coin-three.png");
        }
        &.p4 {
          width: 36px;
          height: 36px;
          background-image: url("../../assets/images/coin-down.png");
          align-self: center;
        }
      }
    }
    &-leaderboard {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 5;
      padding-top: 18px;
      .progress-circle {
        position: relative;
        margin-bottom: 32px;
      }
      .progress-ring1 {
        position: absolute;
        top: 0px;
        left: 103px;
      }
      .progress-ring2 {
        position: absolute;
        top: 10px;
        left: 113px;
      }
      .progress-ring-1 {
        width: 130px;
        height: 130px;
        border: 12px solid rgba(237, 241, 250, 0.3);
        border-radius: 50%;
        position: relative;
        margin: 0 auto;
      }
      .progress-ring-2 {
        width: 106px;
        height: 106px;
        position: absolute;
        display: flex;
        top: 0px;
        left: 0px;
        border: 12px solid rgba(237, 241, 250, 0.1);
        border-radius: 50%;
      }
      .image-container {
        width: 55px;
        height: 56px;
        background-image: url("../../assets/images/ouno-justice.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: auto;
        margin: auto;
      }
      &-choice-values {
        margin-bottom: 26px;
        .choice {
          &-group {
            display: flex;
            justify-content: space-between;
            max-width: 270px;
            margin: 0 auto;
            margin-bottom: 12px;
            align-items: center;
            font-size: 14px;
            line-height: 20px;
          }
          &-name {
            font-weight: 500;
            color: #fff;
          }
          &-value {
            color: #fff;
            opacity: 0.5;
          }
          &-graph {
            width: 68px;
            height: 21px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            &.ch-peop {
              background-image: url("../../assets/images/peoples-choice-graph.svg");
              margin-left: 10px;
            }
            &.ch-ouno {
              background-image: url("../../assets/images/ounos-choice-graph.svg");
            }
          }
        }
      }
      &-rating {
        margin: 0 auto;
        padding: 0 34px;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        &-title {
          margin-bottom: 10px;
        }
        &-list-item {
          display: flex;
          font-size: 15px;
          line-height: 22px;
          justify-content: space-between;
          margin-bottom: 15px;
          p {
            min-width: 192px;
          }
          span {
            color: #9292c1;
          }
        }
      }
    }
    &-peoples-choice {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 4;
      grid-row-end: 6;
      padding: 16px 34px;
      &-title {
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        margin-bottom: 16px;
      }
      &-image {
        height: 200px;
        background-image: url("../../assets/images/peoples-choise.png");
        background-position: center;
        background-size: cover;
        border-radius: 14px;
        background-color: rgba(237, 241, 250, 0.1);
        background-repeat: no-repeat;
        margin-bottom: 16px;
      }
      &-info {
        color: $text;
        font-size: 16px;
        line-height: 24px;
        &-title {
          display: flex;
          align-items: center;
          margin-bottom: 16px;
          &::before {
            content: "";
            display: block;
            width: 15px;
            height: 10px;
            border-radius: 6px;
            background: $blueGrad;
            margin-right: 16px;
          }
        }
        &-received {
          display: flex;
          align-items: baseline;
          span {
            color: #fff;
            font-size: 24px;
            line-height: 28px;
            margin-left: 9px;
            // align-self: flex-start;
          }
          &::before {
            content: "";
            display: block;
            width: 15px;
            height: 10px;
            border-radius: 6px;
            background: $blueGrad;
            margin-right: 16px;
          }
        }
      }
    }
    &-ouno-choice {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 4;
      grid-row-end: 6;
      padding: 16px 34px;
      &-title {
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #fff;
        margin-bottom: 16px;
      }
      &-image {
        height: 200px;
        background-image: url("../../assets/images/ounos-choise.png");
        background-position: center;
        background-size: contain;
        border-radius: 14px;
        background-color: rgba(237, 241, 250, 0.1);
        background-repeat: no-repeat;
        margin-bottom: 16px;
      }
      &-info {
        color: $text;
        font-size: 16px;
        line-height: 24px;
        &-title {
          display: flex;
          align-items: center;
          margin-bottom: 16px;
          &::before {
            content: "";
            display: block;
            width: 15px;
            height: 10px;
            border-radius: 6px;
            background: $purpleGrad;
            margin-right: 16px;
          }
        }
        &-received {
          display: flex;
          align-items: baseline;
          span {
            color: #fff;
            font-size: 24px;
            line-height: 28px;
            margin-left: 9px;
            // align-self: flex-start;
          }
          &::before {
            content: "";
            display: block;
            width: 15px;
            height: 10px;
            border-radius: 6px;
            background: $purpleGrad;
            margin-right: 16px;
          }
        }
      }
    }
    &-favorites {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 5;
      grid-row-end: 6;
      display: flex;
      padding: 16px 34px;
      align-items: center;
      justify-content: space-between;
      font-size: 16px;
      line-height: 24px;
      color: #fff;
      &-icon {
        width: 92px;
        height: 92px;
        background-image: url("../../assets/images/favorites-icon.svg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        margin-right: 20px;
      }
      &-text {
        max-width: 205px;
      }
      &-title {
        font-weight: 700;
        margin-bottom: 8px;
      }
      &-descrip {
        font-weight: 400;
        opacity: 0.6;
      }
    }
    .card {
      border-radius: 23px;
      background-color: $forcards;
    }
  }
  &__explore {
    margin-bottom: 32px;
    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #fff;
    }
    &-title {
      font-weight: bold;
      font-size: 20px;
      line-height: 22px;
      margin-bottom: 32px;
    }
    span {
      font-size: 600px;
      color: $text;
      font-size: 16px;
      line-height: 24px;
      cursor: pointer;
    }
    &-items-container {
      display: flex;
      align-items: center;
    }
    &-item {
      &.explore-trends {
        width: 17%;
        color: #fff;
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
        margin-right: 32px;
        padding: 89px 0;
        text-align: center;
        background-image: url("../../assets/images/bushfire.png");
        background-position: center;
        background-size: cover;
        border-radius: 5px;
        background-repeat: no-repeat;
        &:last-child {
          margin-right: 0;
        }
      }
      &.explore-news {
        width: 33.3%;
        margin-right: 32px;
        &:last-child {
          margin-right: 0;
        }
      }
      &-image {
        height: 176px;
        background-image: url("../../assets/images/news-img.jpg");
        background-position: center;
        background-size: cover;
        border-radius: 15px;
        background-repeat: no-repeat;
        margin-bottom: 16px;
      }
      &-content {
        display: flex;
        justify-content: space-between;
      }
      &-info {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        span.theme {
          color: $text;
          padding: 4px 8px;
          font-weight: 600;
          font-size: 11px;
          line-height: 16px;
          text-align: center;
          background: rgba(65, 67, 72, 0.5);
          border-radius: 4px;
          margin-bottom: 5px;
        }
        span.time {
          font-size: 12px;
          line-height: 18px;
          color: $text;
          position: relative;
          &::before{
            content: '';
            position: absolute;
            display: block;
            width: 14px;
            height: 14px;
            background-image: url('../../assets/images/clock-icon.svg');
            background-position: center;
            background-repeat: no-repeat;
            left: -20px;
            top: 2px;
          }
        }
      }
      &-title {
        color: #fff;
        max-width: 224px;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
      }
    }
  }
}
