@import '../../../scss/colors';

.registration-cause__modal {
  &-close {
    width: 23px;
    height: 23px;
    display: block;
    margin-right: 0;
    margin-left: auto;
    background-image: url('../../../assets/images/close-icon.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  &-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #fff;
    margin-bottom: 24px;
  }
  p {
    font-size: 18px;
    line-height: 27px;
    color: $text;
    margin-bottom: 24px;
  }
  .img {
    width: 234px;
    height: 234px;
    background-image: url('../../../assets/images/registerCause-descrip-img.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
    margin-bottom: 24px;
  }
  button {
    border: none;
    margin-bottom: 24px;
    padding: 20px 70px;
    border-radius: 100px;
    background: $purpleGrad;
    color: #fff;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    line-height: 14px;
  }
}
