@import "../../scss/colors";
@import "../../scss/fonts";

.navigation {
  display: flex;
  flex-direction: column;
  margin-right: 43px;

  .nav {
    &-logo {
      width: 80px;
      height: 80px;
      background-color: $forcards;
      border-radius: 23px;
      background-image: url("../../assets/images/logo.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: auto;
      margin-bottom: 32px;
    }

    &-list {
      width: 80px;
      background-color: $forcards;
      padding: 10px;
      border-radius: 23px;
      margin-bottom: 30px;

      &_main {
        margin-bottom: 280px;
      }

      &_item {
        width: 60px;
        height: 60px;
        display: block;
        border-radius: 19px;
        margin: 24px 0;
        background-color: transparent;
        background-position: center;
        background-size: auto;
        background-repeat: no-repeat;
        position: relative;

        &::after {
          color: $text;
          display: block;
          position: absolute;
          white-space: nowrap;
          background-color: $forcards;
          font-size: 20px;
          line-height: 30px;
          padding: 15px 16px 15px 12px;
          border-radius: 0px 23px 23px 0px;
          left: 65px;
          transition: 0.2s;
          top: 0;
          visibility: hidden;
          opacity: 0;
        }

        &:hover::after {
          opacity: 1;
          visibility: visible
        }

        &.our-mission {
          background-image: url("../../assets/images/earth-menu-icon.svg");

          &::after {
            content: "Our Mission";
          }

          &.active {
            background-image: url("../../assets/images/earth-menu-icon-w.svg"),
            $purpleGrad;
          }
        }

        &.support {
          background-image: url("../../assets/images/fist-menu-icon.svg");

          &::after {
            content: "Support";
          }

          &.active {
            background-image: url("../../assets/images/fist-menu-icon-w.svg"),
            $purpleGrad;
          }
        }

        &.discover {
          background-image: url("../../assets/images/socialCauseList-menu-icon.svg");

          &::after {
            content: "Discover";
          }

          &.active {
            background-image: url("../../assets/images/socialCauseList-menu-icon-w.svg"),
            $purpleGrad;
          }
        }

        &.register {
          background-image: url("../../assets/images/registerCause-menu-icon.svg");

          &::after {
            content: "Register";
          }

          &.active {
            background-image: url("../../assets/images/registerCause-menu-icon-w.svg"),
            $purpleGrad;
          }
        }

        &.suggest {
          background-image: url("../../assets/images/suggest-menu-icon.svg");

          &::after {
            content: "Suggest";
          }

          &.active {
            background-image: url("../../assets/images/suggest-menu-icon-w.svg"),
            $purpleGrad;
          }
        }

        &.help-center {
          background-image: url("../../assets/images/helpCenter-menu-icon.svg");

          &::after {
            content: "Help Center";
          }

          &.active {
            background-image: url("../../assets/images/helpCenter-menu-icon-w.svg"),
            $purpleGrad;
          }
        }
      }
    }
  }
}
