@import '../../../scss/fonts';
@import '../../../scss/colors';

.transfer {
  width: 100%;
  height: 100%;
  border: 1px solid #414348;
  border-radius: 8px;
  background-color: $forcards;
  margin-bottom: 30px;
  &-head {
    &__content {
      display: flex;
      align-self: center;
      padding: 16px 32px;
      border-bottom: 1px solid #414348;
      &__title {
        color: #ffffff;
        font-weight: 600;
        font-size: 24px;
        padding-left: 20px;
        display: flex;
        align-items: center;
      }
    }
  }
  &-content {
    padding: 20px 32px;
  }
  &-subtitle {
    color: #ffffff;
    padding: 32px 0 16px 0;
  }
  &-input {
    border: 1px solid #ffffff;
    padding: 22px;
    width: 100%;
    border-radius: 40px;
    display: flex;
    align-items: center;
    margin-top: 17px;
    &_input {
      background: transparent;
      outline: none;
      color: #ffffff;
      width: 100%;
      border: none;
      height: 100%;
      padding-left: 16px;
    }
  }
  &-dollar {
    font-size: 24px;
    color: #ffffff;
    font-weight: 600;
    user-select: none;
  }
  &-label {
    color: rgba(255, 255, 255, 0.5);
    padding-top: 16px;
  }
  &-btn {
    &__transfer {
      color: #ffffff;
      padding: 15px 100px;
      border-radius: 30px;
      border: none;
      background: $purpleGrad;
      cursor: pointer;
    }
    &__cancel {
      color: #ffffff;
      padding: 15px 100px;
      border-radius: 30px;
      border: 1px solid rgba(255, 255, 255, 0.5);
      background: transparent;
      cursor: pointer;
      margin-left: 20px;
    }
  }
}
