@import '../../scss/colors';
@import '../../scss/fonts';

.wallet {
  width: 100%;
  border-radius: 10px;
  background-color: $forcards;
  margin-bottom: 30px;
  border: 1px solid #414348;
  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #414348;
    padding: 20px 32px;
    &__title {
      font-weight: bold;
      color: #fff;
      font-size: 24px;
      line-height: 32px;
      text-transform: capitalize;
      display: flex;
      align-items: center;
    }
    &__modal {
      color: rgba(255, 255, 255, 0.5);
      border: 2px solid rgba(255, 255, 255, 0.5);
      align-self: flex-end;
      padding: 7px 12px;
      border-radius: 10px;
      font-size: 18px;
      font-weight: 600;
      cursor: pointer;
    }
  }
  &-content {
    display: flex;
    flex-direction: column;
    padding: 20px;
  }

  &-money {
    display: flex;
    background: $mainbg;
    width: 100%;
    padding: 20px;
    border-radius: 8px;
    &__icon {
      margin-right: 20px;
    }
    &__price {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      &__counter {
        color: #ffffff;
        font-size: 24px;
        font-weight: 600;
        padding-bottom: 5px;
      }
      &__btn {
        background-color: $forcards;
        color: rgba(255, 255, 255, 0.5);
        font-weight: 600;
        font-size: 14px;
        padding: 10px 25px;
        border-radius: 25px;
        text-transform: capitalize;
        cursor: pointer;
      }
    }
  }

  &-subtitle {
    color: #ffffff;
    text-transform: capitalize;
    font-weight: 600;
    font-size: 20px;
    padding: 20px 0;
  }

  &-icon {
    align-self: center;
    padding-top: 73px;
    &__description {
      color: rgba(255, 255, 255, 0.5);
      text-align: center;
      line-height: 24px;
      font-weight: 600;
    }
  }
}
.wallet-modal {
  display: flex;
  flex-direction: column;
  &__close {
    align-self: flex-end;
    cursor: pointer;
    padding-bottom: 25px;
  }
  &__title {
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
  }
  &__description {
    color: #96a7af;
    line-height: 24px;
    padding: 25px 0;
    text-align: center;
  }
  &__btn {
    align-self: center;
    border: none;
    background: $purpleGrad;
    color: #ffffff;
    font-weight: 600;
    padding: 15px 100px;
    border-radius: 30px;
    cursor: pointer;
  }
}
