@import '../../../scss/colors';

.help-center-faq {
  width: 100%;

  &__title {
    padding: 32px 32px 86px 32px;
    background-color: $forcards;
    border-radius: 13px;

    &__content {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      img {
        height: 200px;
        width: 200px;
      }
      &__logo {
        color: #ffffff;
        font-size: 58px;
        font-weight: 700;
        padding-top: 25px;
        letter-spacing: 0.2px;
        user-select: none;
      }
      &__second {
        font-weight: 700;
        font-size: 24px;
        padding-top: 10px;
        color: #96a7af;
        user-select: none;
      }
    }
  }
  .help-center-feedback__form {
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    h3 {
      color: #ffffff;
    }
    ul {
      padding: 20px 0 30px 0;
      display: flex;
      margin: 0 -10px;
      li {
        padding: 0 10px;
      }
    }
    &__improved {
      &__list {
        &__item {
          color: #ffffff;

          span {
            display: flex;
            align-items: center;
            background-color: #272a33;
            padding: 10px 15px;
            color: rgba(255, 255, 255, 0.5);
            border-radius: 8px;
            cursor: pointer;
            img {
              margin-right: 10px;
            }
          }
        }
      }
    }
    &__btn {
      background: $purpleGrad;
      padding: 15px 100px;
      font-weight: 600;
      color: #ffffff;
      border-radius: 30px;
      align-self: center;
      margin-top: 16px;
      border: none;
      cursor: pointer;
      margin-top: 20px;
      text-transform: capitalize;
    }
    textarea {
      width: 100%;
      background-color: inherit;
      border: 1px solid #ffffff;
      border-radius: 26px;
      padding: 20px;
      resize: none;
      outline: none;
      color: #ffffff;
    }
  }
}
