@import '../../scss/fonts';
@import '../../scss/colors';

.section-discover {
  width: 100%;

  &-causes {
    padding-bottom: 20px;

    &__title {
      font-weight: bold;
      color: #fff;
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 32px;
      text-transform: capitalize;
    }

    &__search {
      display: flex;
      align-items: center;

      img {
        margin-right: 20px;
        cursor: pointer;
      }
    }

    &__filtration {
      display: flex;
      align-items: center;
      width: 100%;

      &__input {
        display: flex;
        align-items: center;
        background-color: $forcards;
        padding: 20px;
        width: 100%;
        border-radius: 25px;

        img {
          height: 24px;
          width: 24px;
        }

        input {
          width: 100%;
          height: 100%;
          border: none;
          outline: none;
          background-color: inherit;
          color: #fafafa;
          padding-left: 15px;

          &::placeholder {
            color: #fefefe;
          }
        }
      }

      &__sort {
        background-color: $forcards;
        padding: 20px;
        border-radius: 25px;
        cursor: pointer;
        margin-left: 25px;
      }
    }
  }

  &-find__list {
    display: flex;
    margin: 0 -15px;
    flex-wrap: wrap;

    &__item {
      width: 33.3333%;
      box-sizing: border-box;
      padding: 0 15px;

      a {
        text-decoration: none;

        .content_item {
          margin-bottom: 20px;
          color: #fff;
          font-weight: bold;
          font-size: 16px;
          line-height: 22px;
          padding: 89px 0;
          text-align: center;
          background-position: center;
          background-size: cover;
          border-radius: 5px;
          background-repeat: no-repeat;
          position: relative;
          text-shadow: 0 3px 6px rgba(0, 0, 0, 0.7);

          img {
            position: absolute;
            top: 10px;
            right: 10px;
          }
        }
      }
    }
  }

  &-recently-added {
    padding-bottom: 30px;

    &__title {
      font-weight: bold;
      color: #fff;
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 32px;
      text-transform: capitalize;
    }

    &__list {
      display: flex;
      margin: 0 -15px;

      &__item {
        width: 25%;
        margin: 0 15px;
        color: #fff;
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
        padding: 89px 0;
        text-align: center;
        background-position: center;
        background-size: cover;
        border-radius: 5px;
        background-repeat: no-repeat;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.7);
      }
    }
  }

  &-discover {
    &__title {
      font-weight: bold;
      color: #fff;
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 32px;
      text-transform: capitalize;
    }

    &__list {
      display: flex;
      margin: 0 -15px;
      flex-wrap: wrap;

      &__item {
        width: 33.3333%;
        box-sizing: border-box;
        padding: 0 15px;

        a {
          text-decoration: none;

          .content_item {
            margin-bottom: 20px;
            color: #fff;
            font-weight: bold;
            font-size: 16px;
            line-height: 22px;
            padding: 89px 0;
            text-align: center;
            background-position: center;
            background-size: cover;
            border-radius: 5px;
            background-repeat: no-repeat;
            position: relative;
            text-shadow: 0 3px 6px rgba(0, 0, 0, 0.7);

            img {
              position: absolute;
              top: 10px;
              right: 10px;
            }
          }
        }
      }
    }
  }

  &-empty {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__title {
      color: #fff;
      font-size: 22px;
      font-weight: 700;
      padding: 20px 0;
    }

    &__description {
      color: #96a7af;
      text-align: center;
      line-height: 20px;
    }
  }
}
