@import '../../scss/colors';
@import '../../scss/fonts';

.footer {
  background-color: $forcards;
  &-container {
    display: flex;
    padding: 30px 0;
    justify-content: space-around;
    align-items: center;
  }
  .rights-reserved {
    color: #fff;
    font-size: 14px;
    line-height: 21px;
  }
  &-socials {
    display: flex;
    align-items: center;
    &-icon {
      width: 39px;
      height: 39px;
      border-radius: 50%;
      border: 1px solid rgba(255, 255, 255, 0.25);
      background-size: auto;
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;
      margin-right: 14px;
      &:first-child {
        background-image: url("../../assets/images/facebook-icon.svg");
      }
      &:nth-child(2) {
        background-image: url("../../assets/images/linkedin-icon.svg");
      }
      &:nth-child(3) {
        background-image: url("../../assets/images/twitter-icon.svg");
      }
      &:last-child {
        background-image: url("../../assets/images/instagram-icon.svg");
        margin-right: 0;
      }
    }
  }
}
