@import '../../../scss/colors';

.share-modal {
  &-window {
    &__close {
      text-align: end;
      padding-bottom: 15px;

      img {
        cursor: pointer;
        height: 16px;
        width: 16px;
      }
    }

    &__title {
      color: #fff;
      font-weight: 600;
      font-size: 24px;
      text-align: center;
      line-height: 120%;
      padding-bottom: 30px;
    }

    &__list {
      display: flex;
      justify-content: center;

      &__item {
        padding: 0 10px;

        a {
          img {
          }
        }
      }
    }

    &__copy {
      font-size: 16px;
      line-height: 120%;
      text-align: center;
      letter-spacing: 0.741087px;
      padding: 20px 0;
    }

    &__link {
      display: flex;
      justify-content: center;

      &__test {
        background-color: #000;
        padding: 10px;
        border-radius: 17px;
        display: flex;
        justify-content: center;
        width: 370px;

        input {
          border: none;
          outline: none;
          background-color: rgba(0, 0, 0, 0);
          color: #ffffff;
          width: 100%;
          padding-left: 10px;
          font-size: 18px;
        }

        button {
          border: none;
          font-size: 18px;
          font-weight: 600;
          cursor: pointer;
          background-image: $purpleGrad;
          background-size: 100%;
          -webkit-background-clip: text;
          -moz-background-clip: text;
          -webkit-text-fill-color: transparent;
          -moz-text-fill-color: transparent;
        }
      }
    }
  }
}
