@import '../../scss/colors';

.people-winner {
  width: 100%;
  padding-bottom: 30px;
  &__title {
    background-color: $forcards;
    padding: 27px 32px;
    border: 1px solid #414348;
    border-radius: 20px;
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: 700;
    text-transform: capitalize;
    color: #ffffff;
    margin-bottom: 18px;
    a {
      margin-right: 18px;
    }
  }
}
.wrapper__choice {
  display: flex;
  margin: 0 -25px;
}
.choice {
  width: 50%;
  padding: 0 25px;
  box-sizing: border-box;
  &__card {
    background-color: $forcards;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    padding: 14px 16px;
    &__title {
      padding-top: 24px;
      align-self: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      &__logo {
        color: #ffffff;
        font-size: 22px;
        font-weight: 700;
        padding-bottom: 10px;
      }
      &__content {
        &__causes {
          color: rgba(255, 255, 255, 0.65);
          padding-top: 15px;
          position: relative;
          &.ouno {
            &::before {
              content: '';
              position: absolute;
              left: -15px;
              top: 100%;
              transform: translate(-50%, -135%);
              width: 15px;
              height: 10px;
              border-radius: 6px;
              background: $purpleGrad;
            }
          }
          &.people {
            &::before {
              content: '';
              position: absolute;
              left: -15px;
              top: 100%;
              transform: translate(-50%, -135%);
              width: 15px;
              height: 10px;
              border-radius: 6px;
              background: $blueGrad;
            }
          }
        }
        &__received {
          color: #ffffff;
          opacity: 0.65;
          font-size: 14px;
          text-transform: capitalize;
          padding: 8px 0 5px 0;
        }
        &__amount {
          font-size: 28px;
          color: #ffffff;
          position: relative;
          &.ouno {
            &::before {
              content: '';
              position: absolute;
              left: -15px;
              top: 50%;
              transform: translate(-50%, -50%);
              width: 15px;
              height: 10px;
              border-radius: 6px;
              background: $purpleGrad;
            }
          }
          &.people {
            &::before {
              content: '';
              position: absolute;
              left: -15px;
              top: 50%;
              transform: translate(-50%, -50%);
              width: 15px;
              height: 10px;
              border-radius: 6px;
              background: $blueGrad;
            }
          }
        }
      }
    }
    &__previous {
      color: #ffffff;
      font-weight: 700;
      padding: 15px 0;
    }
    &__button {
      background: $purpleGrad;
      padding: 15px 30px;
      font-weight: 600;
      color: #ffffff;
      border-radius: 30px;
      align-self: center;
      margin-top: 16px;
      text-transform: capitalize;
    }
  }
}
