@import '../../scss/colors';
@import '../../scss/fonts';

.support {
  width: 100%;
  padding-bottom: 30px;
  &-causes {
    &__title {
      font-weight: bold;
      color: #fff;
      font-size: 24px;
      line-height: 32px;
      //margin-bottom: 32px;
    }

    &__list {
      display: flex;
      margin: 0 -15px;
      padding-bottom: 32px;
      &__item {
        width: 25%;

        padding: 0 15px;
        &__content {
          color: #fff;
          font-weight: bold;
          font-size: 16px;
          line-height: 22px;
          padding: 89px 0;
          text-align: center;
          background-position: center;
          background-size: cover;
          border-radius: 5px;
          box-sizing: border-box;
          background-repeat: no-repeat;
          height: 100%;
          text-shadow: 0 3px 6px rgba(0, 0, 0, 0.7);
        }
        &.selected {
          width: 33%;
        }
      }
    }
  }
  &__subtitle {
    color: #ffffff;
    font-size: 20px;
    padding-bottom: 25px;
    font-weight: 600;
  }
  &-contributing {
    display: flex;
    flex-direction: column;
    &__amount {
      padding-bottom: 30px;
      &__list {
        display: grid;
        grid-template-columns: repeat(4, 24%);
        gap: 15px;
        &__item {
          background-color: $forcards;
          color: #ffffff;
          border-radius: 8px;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 10px 0;
          position: relative;

          &.input {
            div {
              display: flex;
              align-items: center;
              justify-content: center;
              img {
                align-self: center;
                transition: all 0.3s linear;
              }
              input {
                display: none;
                background: none;
                border: none;
                width: 90px;
                -moz-appearance: textfield;
                margin: 0;
                font-size: 16px;
                color: #ffffff;
                outline: none;
                &::-webkit-inner-spin-button,
                &::-webkit-outer-spin-button {
                  -webkit-appearance: none;
                }
              }
              span {
                display: none;
              }
            }
            &.active {
              div {
                justify-content: space-evenly;
                width: 100%;
                &::before {
                  content: '';
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  right: 0;
                  width: 100%;
                  height: 8px;
                  background: $purpleGrad;
                  border-radius: 0 0 10px 10px;
                }
                input {
                  display: block;
                  background: none;
                  border: none;
                  width: 90px;
                  -moz-appearance: textfield;
                  margin: 0;
                  font-size: 16px;
                  color: #ffffff;
                  outline: none;
                  font-size: 24px;
                  font-weight: 600;
                  &::-webkit-inner-spin-button,
                  &::-webkit-outer-spin-button {
                    -webkit-appearance: none;
                  }
                }
                span {
                  display: block;
                }
              }
            }
          }
          &.fix {
            transition: all 0.6s linear;
            font-size: 24px;
            &:hover {
              transition: all 0.6s linear;
              background: $purpleGrad;
            }
            &.active {
              background: $purpleGrad;
              font-weight: 600;
            }
          }
        }
      }
    }
  }
  &-btn {
    color: #96a7af;
    font-size: 14px;
    font-weight: 600;
    background: $forcards;
    border: none;
    padding: 15px 40px;
    border-radius: 23px;
    cursor: pointer;
    text-transform: capitalize;
    align-self: center;
    margin-top: 40px;
  }
}
.support-modal {
  display: flex;
  flex-direction: column;
  &__close {
    align-self: flex-end;
    cursor: pointer;
    padding-bottom: 25px;
  }
  &__title {
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
  }
  &__description {
    color: #96a7af;
    line-height: 24px;
    padding: 25px 0;
    text-align: center;
  }
  &__btn {
    align-self: center;
    border: none;
    background: $purpleGrad;
    color: #ffffff;
    font-weight: 600;
    padding: 15px 100px;
    border-radius: 30px;
    cursor: pointer;
  }
}

.support-split {
  padding: 32px;
  background-color: $forcards;
  border-radius: 8px;
  &__modal {
    color: rgba(255, 255, 255, 0.5);
    border: 2px solid rgba(255, 255, 255, 0.5);
    align-self: flex-end;
    padding: 7px 12px;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
  }
}
.split__price {
  display: flex;
  justify-content: space-between;
  padding-bottom: 30px;
  &__bar {
    display: flex;
    img {
      padding: 13px;
      border: 1px solid #363661;
      border-radius: 8px;
      margin-right: 15px;
      align-self: flex-start;
    }
    &__title {
      white-space: nowrap;
      color: #ffffff;
      padding-bottom: 5px;
    }
    &__percent {
      font-size: 26px;
      font-weight: 700;
      color: #fff;
      padding-bottom: 5px;
    }
    &__choice {
      color: rgba(255, 255, 255, 0.5);
    }
    &__input {
      -webkit-appearance: none;
      margin-right: 15px;
      width: 100%;
      height: 28px;
      background: rgba(255, 255, 255, 0.2);
      border-radius: 20px;
      background-image: $blueGrad;
      background-repeat: no-repeat;
      cursor: pointer;
      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        background-image: url('../../assets/images/play-section-rocket-man.svg');
        height: 57px;
        width: 57px;
        cursor: pointer;
        transition: background 0.3s ease-in-out;
      }
      &::-moz-range-thumb {
        -webkit-appearance: none;
        background-image: url('../../assets/images/play-section-rocket-man.svg');
        height: 57px;
        width: 57px;
        cursor: pointer;
        transition: background 0.3s ease-in-out;
      }
      &::-ms-thumb {
        -webkit-appearance: none;
        background-image: url('../../assets/images/play-section-rocket-man.svg');
        height: 57px;
        width: 57px;
        cursor: pointer;
        transition: background 0.3s ease-in-out;
      }
      &::-webkit-slider-runnable-track {
        -webkit-appearance: none;
        box-shadow: none;
        border: none;
        background: transparent;
      }
      &::-moz-range-track {
        -webkit-appearance: none;
        box-shadow: none;
        border: none;
        background: transparent;
      }
      &::-ms-track {
        -webkit-appearance: none;
        box-shadow: none;
        border: none;
        background: transparent;
      }
    }
  }
  &__center-bar {
    width: 100%;
    display: flex;
    margin: 0 16px;
    .passive-bar {
      background-color: #000000;
      height: 6px;
      width: 100%;
      display: flex;
      align-self: center;
      .active-bar {
        &_ouno {
          height: 100%;
          background: $purpleGrad;
          border-radius: 10px;
        }
        &_people {
          height: 100%;
          border-radius: 10px;
          background: $blueGrad;
        }
      }
    }
  }
  &__right_bar {
    align-self: center;
  }
}
