@import '../../../scss/colors';

.help-center {
  width: 100%;
  &__video {
    width: 100%;
    height: 400px;
  }
}
.navigation-list {
  &__item {
    margin: 20px 0;
    background-color: $forcards;
    border-radius: 8px;
    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px;
      text-decoration: none;
      color: #ffffff;
      text-transform: capitalize;
      font-size: 18px;
      font-weight: 700;
      .button {
        display: flex;
        align-items: center;
        img {
          margin-right: 20px;
        }
      }
    }
  }
}
