@import "../../scss/colors";
@import "../../scss/fonts";
.header {
  padding: 25px 0;
  .header-content {
    display: flex;
  }
  &-logo {
    width: 80px;
    height: 80px;
    background-color: $forcards;
    border-radius: 23px;
    background-image: url("../../assets/images/logo.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
  }
  &-content-values {
    background-color: $forcards;
    display: flex;
    justify-content: space-between;
    border-radius: 23px;
    padding: 0 16px;
    width: 100%;
    margin-left: 25px;
    position: relative;
    z-index: 0;
  }
  &-values {
    display: flex;
    align-items: center;
    .label {
      font-size: 20px;
      line-height: 20px;
      color: #fff;
      margin-right: 20px;
    }
    .value {
      font-weight: bold;
      font-size: 28px;
      line-height: 2px;
      color: #fff;
    }
    &-raised {
      background: $blueGrad;
      padding: 16px 24px;
      border-radius: 19px;
      margin-right: 17px;
      a{
        //text-decoration: none;
        display: flex;
        align-items: center;
      }
    }
    &-countdown {
      display: flex;
      align-items: center;
      background: $purpleGrad;
      padding: 16px 24px;
      border-radius: 19px;
    }
  }
  &-profile {
    display: flex;
    align-items: center;
    &-container {
      display: flex;
      align-items: center;
      height: fit-content;
      border: 1px solid $text;
      border-radius: 26px;
      padding-right: 70px;
      &:hover {
        cursor: pointer;
      }
      p {
        font-weight: 600;
        font-size: 12px;
        line-height: 18px;
        color: #fff;
      }
      &--authorized {
        border-color: transparent;
        padding-right: 10px;
      }
    }
    &-menu {
      position: relative;
      z-index: 0;
      margin-right: 15px;
      min-width: 250px;
    }
    &-submenu {
      position: absolute;
      top: -1px;
      left: -1px;
      background-color: $forcards;
      display: none;
      padding: 0 5px;
      padding-top: 60px;
      padding-bottom: 20px;
      border: 1px solid $text;
      z-index: -1;
      border-radius: 26px;
      &--display {
        display: block;
      }
    }
    &-flex-row {
      display: flex;
      align-items: center;
    }
    &-icon {
      align-self: flex-start;
      margin-right: 15px;
    }
    &-link {
      text-decoration: none;
      color: $text;
      margin-top: 10px;
      h3 {
        color: #fff;
      }
      p {
        font-size: 12px;
        line-height: 18px;
      }
      & + & {
        margin-top: 20px;
      }
    }
    &-image {
      width: 52px;
      height: 52px;
      border-radius: 50%;
      background-color: $mainbg;
      background-image: url("../../assets/images/profile-icon.svg");
      background-position: center;
      background-size: auto;
      background-repeat: no-repeat;
      position: relative;
      margin-right: 22px;
      &::after {
        content: "";
        width: 6px;
        height: 6px;
        border-radius: 50%;
        display: block;
        position: absolute;
        background-image: url("../../assets/images/red-dot.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        right: 0;
        padding: 4px;
        border: 1px solid rgba(242, 78, 30, 0.4);
      }
      &--authorized {
        border: none;
      }
      &.header-profile-image--authorized {
        margin-right: 10px;
        &::after {
          content: none;
        }
      }
    }
    &-arrow {
      margin-left: 15px;
    }
    &-coins {
      display: flex;
      align-items: flex-end;
      margin-right: 30px;
      &-icon {
        width: 24px;
        height: 24px;
        background-image: url("../../assets/images/profile-coin.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        margin-right: 4px;
      }
      &-value {
        font-size: 14px;
        font-weight: 600;
        line-height: 21px;
        color: #fff;
      }
    }
  }
}
