@import '../../../scss/colors';
@import '../../../scss/fonts';

@mixin infoIcon($content: '') {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &-icon {
    width: 26px;
    height: 25px;
    display: block;
    background-image: url('../../../assets/images/Info_square.svg');
    background-position: center;
    background-size: cover;
    position: relative;
    &::after {
      content: $content;
      display: block;
      width: 288px;
      padding: 16px 25px;
      position: absolute;
      z-index: 3;
      right: -250px;
      opacity: 0;
      top: 35px;
      border-radius: 16px;
      background-color: #4e4b66;
      color: #fff;
      font-size: $mniFZ;
      visibility: hidden;
      line-height: $mniLH;
    }
    &::before {
      content: '';
      width: 24px;
      height: 24px;
      z-index: 3;
      background-color: #4e4b66;
      display: block;
      top: 32px;
      left: 0;
      transform: rotateZ(45deg);
      position: absolute;
      visibility: hidden;
      opacity: 0;
    }
    &:hover {
      &::after,
      &::before {
        visibility: visible;
        opacity: 1;
      }
    }
  }
}

.registration-form-page {
  &__wrapper {
    max-width: 868px;
    position: relative;
    z-index: 1;
  }
  .form-tabs {
    background-color: #f5f5f5;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 25px;

    &__detail {
      width: 50%;
      padding: 22px 0;
      text-align: center;
      cursor: pointer;
      font-size: 24px;
      font-weight: 500;
      border: none;
      background-color: inherit;
      border-radius: 25px 25px 0 0;
      &.active {
        background-color: #fff;
        border-radius: 25px 25px 0 0;
      }
    }
    &__display {
      width: 50%;
      padding: 22px 0;
      text-align: center;
      cursor: pointer;
      font-size: 24px;
      font-weight: 500;
      border-radius: 25px 25px 0 0;
      border: none;
      background-color: inherit;
      &.active {
        background-color: #fff;
        border-radius: 25px 25px 0 0;
      }
    }
    &__description {
      color: #bdbdbd;
      font-size: $mniFZ;
      line-height: $mniLH;
      font-weight: 400;
      background-color: #ffffff;
      &_detail {
        display: none;
        &.active {
          padding: 33px 80px 0 80px;
          display: block;
        }
      }
      &_display {
        display: none;
        &.active {
          padding: 31px 28px 0 28px;
          display: block;
        }
      }
    }
  }

  .form-content {
    background-color: #ffffff;
    border-radius: 0 0 25px 25px;
    &__list {
      &_details {
        display: none;
        &.active {
          display: block;
          padding: 12px 80px 33px 80px;
        }
      }
      &_display {
        display: none;
        &.active {
          display: flex;
          align-items: center;
          padding: 31px 28px;
        }
      }
    }
    &__send-btn {
      cursor: pointer;
      width: 256px;
      margin: 32px auto;
      border: none;
      border-radius: 100px;
      color: #fff;
      background: $purpleGrad;
      display: block;
      font-size: $btnFZ;
      line-height: $btnLH;
      font-weight: 600;
      padding: 15px 0;
    }
    &__file-wrapper {
      display: flex;
      align-items: center;
    }
    .form-content__file-wrapper + .form-content__file-wrapper {
      margin-left: 10px;
    }
    &__file-name {
      margin: 0 10px;
      font-weight: 400;
      font-size: 11px;
      line-height: 13px;
      color: #151522;
      white-space: nowrap;
    }
    &__file-close {
      &:hover {
        cursor: pointer;
      }
    }
    label {
      background: #f5f5f5;
      border: 1px solid #e6e6e6;
      border-radius: 10px;
      margin-bottom: 12px;
      padding: 18px 25px;
      width: 100%;
      display: block;
      &.form-content__invalid {
        border: 1px solid red;
      }
      &.form-content__label--file {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-family: 'Poppins';
        font-weight: 500;
        font-size: 16px;
        color: #b0b0b0;
        &--invalid {
          border-color: red;
        }
      }
      &.form-content__label--icon {
        width: auto;
        border: none;
        padding: 0;
        margin: 0;
        margin-left: 10px;
        &:hover {
          cursor: pointer;
        }
      }
    }
    input {
      font-family: 'Poppins', 'Roboto', 'Arial', sans-serif;
      outline: none;
      font-size: $mniFZ;
      line-height: $mniLH;
      width: 100%;
      border: none;
      background-color: transparent;
      font-weight: 500;
      &::placeholder {
        font-weight: 500;
        font-size: 16px;
        color: #b0b0b0;
      }
    }
    textarea {
      font-family: 'Poppins', 'Roboto', 'Arial', sans-serif;
      font-size: $mniFZ;
      line-height: $mniLH;
      width: 100%;
      outline: none;
      background-color: #f5f5f5;
      border: none;
      font-weight: 500;
      resize: none;
      &#ds_tagline {
        height: 100%;
        overflow: auto;
      }
      &::placeholder {
        font-weight: 500;
        font-size: 16px;
        color: #b0b0b0;
      }
    }
    .ds_description {
      transition: all 0.3s ease;
      @include infoIcon(
        $content:
          'Describe your cause in detail. Tell your whole story. But be concise and straight to the point. Let everyone know what you do, why you are doing it and what you would do if you receive money through Ouno.'
      );
      align-items: flex-start;
      &-icon {
        margin-top: 5px;
        &::after {
          right: -65px;
        }
      }
    }
    .ds_display-name {
      @include infoIcon(
        $content:
          'Display name is the name which you choose to display as the main name on your tile within the Ouno app - max 60 Characters.'
      );
      &-icon {
        &::after {
          right: -65px;
        }
      }
    }
    .ds_tagline {
      @include infoIcon('Add the tagline of Your Cause');
      &-icon {
        &::after {
          right: -65px;
        }
      }
    }
    .dt_additionalInfo {
      @include infoIcon(
        'Let us know any additional information that we should know.'
      );
    }

    &__display-phone {
      width: 50%;
      height: 590px;
      background-image: url('../../../assets/images/phone-display-form.png');
      background-size: auto;
      background-position: center;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      &__title {
        padding: 14px 55px;
        width: 254px;
        color: #fff;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        border-radius: 30px 30px 0 0;
        position: relative;
        margin-top: 17px;
        margin-bottom: 116px;
        transition: 0.3s;
        span {
          position: relative;
          z-index: 1;
        }
        &::before {
          content: '';
          left: 0;
          top: 0;
          position: absolute;
          width: 100%;
          height: 100%;
          display: block;
          border-radius: 30px 30px 0 0;
          opacity: 0.5;
          background: $purpleGrad;
        }
      }
      &__tagline {
        padding: 12px 60px;
        width: 254px;
        color: #fff;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        text-align: center;
        transition: 0.3s;
        position: relative;
        margin-bottom: 12px;
        span {
          position: relative;
          z-index: 1;
        }
        &::before {
          content: '';
          left: 0;
          top: 0;
          position: absolute;
          width: 100%;
          height: 100%;
          display: block;
          opacity: 0.5;
          background: $purpleGrad;
        }
      }
      &__extraImg {
        padding: 24px 60px;
        width: 254px;
        color: #fff;
        font-weight: 700;
        transition: 0.3s;
        font-size: 20px;
        line-height: 28px;
        text-align: center;
        position: relative;
        span {
          position: relative;
          z-index: 1;
        }
        &::before {
          content: '';
          left: 0;
          top: 0;
          position: absolute;
          width: 100%;
          height: 100%;
          display: block;
          opacity: 0.5;
          background: $purpleGrad;
        }
      }
      &__headlineImg {
        padding: 57px 80px;
        width: 254px;
        color: #fff;
        transition: 0.3s;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        position: absolute;
        text-align: center;
        top: 17px;
        span {
          position: relative;
          z-index: 1;
        }
        &::before {
          content: '';
          left: 0;
          top: 0;
          border-radius: 30px 30px 0 0;
          position: absolute;
          width: 100%;
          height: 100%;
          display: block;
          opacity: 0.5;
          background: $purpleGrad;
        }
      }
      &__description {
        padding: 67px 68px;
        width: 254px;
        transition: 0.3s;
        color: #fff;
        font-weight: 700;
        font-size: 20px;
        line-height: 28px;
        border-radius: 0 0 30px 30px;
        position: absolute;
        bottom: 95px;
        text-align: center;
        span {
          position: relative;
          z-index: 1;
        }
        &::before {
          content: '';
          left: 0;
          top: 0;
          position: absolute;
          width: 100%;
          height: 100%;
          display: block;
          opacity: 0.5;
          background: $purpleGrad;
        }
      }
    }
  }
}
.form-privacy-policy {
  //font-weight: medium;
  color: #fff;
  text-align: center;
  margin-bottom: 18px;
  a {
    color: #fff;
  }
}

.agreement {
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  font-weight: 500;
  margin-bottom: 40px;
  color: #fff;
  span {
    color: #fff;
    text-decoration: underline;
    text-decoration-color: #fff;
    font-size: 15px;
