@import '../../scss/colors';
@import '../../scss/fonts';

.error__modal {
  &-close {
    width: 23px;
    height: 23px;
    display: block;
    margin-right: 0;
    margin-left: auto;
    background-image: url('../../assets/images/close-icon.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  &-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 50px;
    color: #fff;
    padding-bottom: 24px;
  }
  &-img {
    width: 230px;
    background-image: url('../../assets/images/error-modal-img.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    height: 132px;
    margin-bottom: 24px;
  }
  &-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: $text;
    max-width: 345px;
    padding-bottom: 24px;
  }
  &-reload {
    background: $purpleGrad;
    color: #fff;
    font-size: 14px;
    line-height: 23px;
    font-weight: 600;
    border: none;
    border-radius: 23px;
    padding: 13px 80px;
    cursor: pointer;
  }
}
