@import '../../scss/colors';
@import '../../scss/fonts';

.registration-cause {
  background-color: $forcards;
  border-radius: 10px;
  width: 100%;
  margin-bottom: 45px;

  &__nav {
    background: $forcards;
    border: 1px solid #414348;
    border-radius: 10px 10px 0 0;
    padding: 27px 32px;
    display: flex;
    align-items: center;
    margin-bottom: 45px;

    &-icon {
      border: 1px solid rgba(255, 255, 255, 0.5);
      border-radius: 9px;
      margin-right: 18px;

      img {
        padding: 10px;
        display: inline-block;
        vertical-align: middle;
      }
    }

    &-title {
      font-size: 24px;
      line-height: 28px;
      font-weight: bold;
      color: #fff;
    }
  }
  &__content {
    display: flex;
    justify-content: center;
  }
}
