@import '../../scss/colors';
.split {
  background-color: #272a33;
  width: 100%;
  margin-bottom: 30px;
  border-radius: 10px;
  border: 1px solid #414348;
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 27px 32px;
    border-bottom: 1px solid #414348;
    &__back {
      display: flex;
      align-items: center;
      color: #ffffff;
      font-size: 24px;
      font-weight: 600;
      text-transform: capitalize;
      img {
        margin-right: 20px;
      }
    }
  }
  &__choice {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
    margin: 0 -15px;
    &__tile {
      background-color: #1c1e24;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 16px 0;
      border-radius: 8px;
      //width: 50%;
      &__icon {
        border: 2px solid #363661;
        border-radius: 8px;
        img {
          padding: 12px;
        }
      }
      &__content {
        padding-left: 24px;
        &__title {
          line-height: 24px;
          color: #ffffff;
          text-transform: capitalize;
        }
        &__money {
          font-size: 24px;
          font-weight: 600;
          color: #ffffff;
        }
      }
    }
  }
}
.pie-diagram {
  display: flex;
  justify-content: center;
  padding-bottom: 40px;
  margin: 0 -60px;
  &__ring {
    padding: 0 60px;
    &__svg {
      transform: rotate(-90deg);
      &__circle {
        &_passive {
          fill: none;
          stroke: #1c1e24;
          stroke-width: 9px;
        }
        &_active {
          position: relative;
          z-index: 1;
          stroke-width: 18px;
          fill: none;
          stroke-linejoin: round;
        }
      }
    }
  }
  &__data {
    padding: 0 60px;
    align-self: center;
    &__title {
      font-size: 20px;
      text-transform: capitalize;
      color: #ffffff;
    }
    &__money {
      font-size: 24px;
      color: #ffffff;
      font-weight: 600;
      padding: 15px 0;
    }
    &__last {
      font-size: 15px;
      opacity: 0.5;
      color: #ffffff;
    }
    &__choice {
      display: flex;
      margin: 0 -15px;
      padding-top: 40px;
      &__percent {
        font-size: 40px;
        font-weight: 600;
        color: #ffffff;
        text-align: center;
        padding-bottom: 5px;
      }
      &__people {
        background: $blueGrad;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        text-transform: capitalize;
        font-weight: 600;
        font-size: 18px;
      }
      &__ouno {
        background: $purpleGrad;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        text-transform: capitalize;
        font-weight: 600;
        font-size: 18px;
      }
      &__graph {
        padding: 0 15px;
        text-transform: capitalize;
        color: #ffffff;
        font-weight: 600;
        //h4{
        //  -webkit-background-clip: text;
        //  -webkit-text-fill-color: transparent;
        //}
      }
    }
  }
}
