@import '../../../scss/colors';

.help-center-faq {
  width: 100%;

  &__title {
    padding: 32px 32px 86px 32px;
    background-color: $forcards;
    border-radius: 13px;
    &__content {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      img {
        height: 200px;
        width: 200px;
      }
      &__logo {
        color: #ffffff;
        font-size: 58px;
        font-weight: 700;
        padding-top: 25px;
        letter-spacing: 0.2px;
        user-select: none;
      }
      &__second {
        font-weight: 700;
        font-size: 24px;
        padding-top: 10px;
        color: #96a7af;
        user-select: none;
      }
    }
  }
  &__accordion {
    padding-top: 39px;
    h4 {
      color: #ffffff;
      font-size: 24px;
      font-weight: 700;
    }
    &__list {
      padding: 48px 0;
      &__item {
        padding: 24px 0;
        border-bottom: 1px solid rgba(65, 67, 72, 0.5);
        &.active {
          .help-center-faq__accordion__list__item__description {
            transition: all 0.3s ease;
            opacity: 1;
            visibility: visible;
            padding-top: 34px;
            max-height: 500px;
          }
          .help-center-faq__accordion__list__item__title {
            img {
              transition: all 0.3s ease;
              transform: rotate(180deg);
            }
          }
        }
        &__title {
          display: flex;
          align-items: center;
          justify-content: space-between;
          cursor: pointer;
          h5 {
            color: #ffffff;
            font-size: 20px;
          }
          img {
            transition: all 0.3s ease;
          }
        }
        &__description {
          font-weight: 500;
          font-size: 16px;
          line-height: 28px;
          color: #ffffff;
          max-height: 0;
          opacity: 0;
          visibility: hidden;
          transition: all 0.3s ease;
        }
      }
    }
  }
}
