@import '../../scss/colors';
@import '../../scss/fonts';

.funds-raised {
  width: 100%;
  background-color: $forcards;
  padding-bottom: 32px;
  border: 1px solid #414348;
  border-radius: 10px;
  margin-bottom: 48px;
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 27px 32px;
    border-bottom: 1px solid #414348;
    &__back {
      display: flex;
      align-items: center;
      color: #ffffff;
      font-size: 24px;
      font-weight: 600;
      img {
        margin-right: 20px;
      }
    }
    &__payment {
      padding: 13px 30px;
      background: $purpleGrad;
      border: none;
      color: #ffffff;
      border-radius: 23px;
      font-weight: 600;
      cursor: pointer;
    }
  }
  &__weeks-progress {
    padding: 25px 0;
    border-radius: 8px;
    color: #ffffff;
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    text-transform: capitalize;
    margin: 40px 32px 0 32px;
    img {
      margin-right: 18px;
    }
    h4 {
    }
  }
  &__post {
    margin: 40px 0;
    display: flex;
    justify-content: space-between;
    padding: 0 32px;
    &__button {
      color: #ffffff;
      &_left {
        font-size: 18px;
        font-weight: 700;
        color: #ffffff;
      }
    }
  }
  &__diagram {
    margin: 0 auto 40px auto;
  }
  &__current-help {
    display: flex;
    align-items: center;
    padding: 16px 22px;
    margin: 8px 32px 0 32px;
    background-color: $mainbg;
    border-radius: 8px;
    img {
      margin-right: 16px;
    }
    h5 {
      color: #ffffff;
      font-weight: 400;
    }
  }
}
