@import '../../../scss/fonts';
@import '../../../scss/colors';

.select-cause-page {
  width: 100%;
  border-radius: 10px;
  background-color: $forcards;
  margin-bottom: 40px;

  &-header {
    border-bottom: 1px solid #414348;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;

    &__title {
      font-weight: bold;
      color: #fff;
      font-size: 24px;
      line-height: 32px;
      text-transform: capitalize;
      display: flex;
      align-items: center;

      img {
        margin-right: 20px;
      }
    }

    &__buttons {
      display: flex;
      align-items: center;

      &__support {
        color: #ffff;
        font-size: 20px;
        font-weight: 600;
        background: $purpleGrad;
        border: none;
        padding: 15px 40px;
        border-radius: 23px;
        cursor: pointer;
        margin-right: 20px;
        text-transform: capitalize;
      }

      &__share {
        cursor: pointer;
      }
    }
  }

  &-main {
    padding: 20px;
    width: 100%;
    height: 500px;

    img {
      width: 100%;
      height: 100%;

      border-radius: 10px;
    }
  }

  &-footer {
    padding: 20px;

    &-container {
      &__title {
        color: #fff;
        font-size: 18px;
        font-weight: 600;
        padding-bottom: 20px;
      }

      &__description {
        color: #96a7af;
        line-height: 25px;
      }
    }
  }
}
