@import '../../scss/colors';
@import '../../scss/fonts';

.authorization-modal {
  &__title {
    margin-bottom: 0.5em;
    display: inline-block;
    font-weight: 700;
    font-size: 32px;
    color: #fff;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 5px;
      left: 0;
      bottom: 3px;
      background: linear-gradient(269.95deg, #d680f1 0.02%, #765beb 97.45%);
      z-index: -1;
    }
  }
  &__left {
    text-align: left;
  }
  &__right {
    text-align: right;
    width: 100%;
  }
  &__skip {
    color: #fff;
    text-decoration: underline;
    font-size: 18px;
    display: inline;
    &:hover {
      cursor: pointer;
    }
  }
  &__back {
    color: #96a7af;
    display: inline;
    position: relative;
    &:hover {
      cursor: pointer;
    }
    &::before {
      content: url('../../assets/images/arrow-left.svg');
      position: absolute;
      left: -28px;
      top: -4px;
      height: 11px;
    }
  }
  &__flex-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & + & {
      margin-top: 17px;
    }
    &--margin {
      margin-bottom: 32px;
      margin-top: 10px;
    }
  }
  &__icon {
    height: 35px;
  }
  &__separator {
    color: #aeaeae;
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: 32px;
    &::before {
      margin-right: 10px;
    }
    &::after {
      margin-left: 10px;
    }
    &::before,
    &::after {
      content: '';
      flex: 1;
      border-bottom: 1px solid #e0e0e0;
    }
  }
  &__short-btn {
    min-width: 198px;
    border-radius: 35px;
    padding: 10px 25px;
    align-items: center;
    font-weight: 600;
    border: 1px solid #c4c4c4;
    background-color: transparent;
    &:hover {
      cursor: pointer;
    }
  }
  &__info {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: $text;
    margin-bottom: 1em;
    text-align: left;
    max-width: 475px;
  }
  &__form {
    margin-bottom: 16px;
  }
  &__text-input {
    text-align: left;
    margin-top: 24px;
  }
  &__label {
    color: #fff;
    text-align: left;
    font-size: 16px;
    text-align: left;
    margin-right: 5px;
    &--error {
      color: #ff3b3b;
    }
    &--required {
      position: relative;
      &::after {
        content: '*';
        color: #ff0000;
        line-height: 34px;
        font-size: 20px;
        position: absolute;
        top: -10px;
      }
    }
  }
  &__input-wrapper--with-error {
  }
  &__input-wrapper {
    margin: 10px 0;
    position: relative;
    &:focus-within {
      &::before {
        content: '';
        position: absolute;
        top: -1px;
        left: -1px;
        bottom: -1px;
        right: -1px;
        z-index: -1;
        border-radius: 8px;
        background: linear-gradient(269.95deg, #d680f1 0.02%, #765beb 97.45%);
      }
    }
    &--with-error {
      position: relative;
      &::after {
        content: url('../../assets/images/Error\ Circle.svg');
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translate(-50%, -50%);
      }
      &:focus-within {
        &::before {
          content: none;
        }
      }
    }
  }
  &__password-wrapper {
    position: relative;
    img {
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translate(-50%, -50%);
      &:hover {
        cursor: pointer;
      }
    }
  }
  &__input {
    font-family: 'Poppins';
    font-size: 16px;
    border-radius: 8px;
    border: none;
    padding: 18px 24px;
    width: 100%;
    outline: none;
    border: 1px solid #e0e0e0;
    &--invalid {
      border: 1px solid #ff3b3b;
      box-shadow: 0px 0px 14px rgba(255, 59, 59, 0.15);
    }
    &::placeholder {
      font-family: 'Poppins';
      font-size: 16px;
    }
  }
  &__link {
    font-weight: 500;
    color: #7b5dd9;
    &:hover {
      cursor: pointer;
    }
  }
  &__email {
    color: #fff;
    display: block;
    font-weight: 600;
  }
  &__btn {
    width: 100%;
    font-family: 'Poppins';
    border-radius: 100px;
    border: none;
    text-align: center;
    text-transform: capitalize;
    padding: 18px 0;
    font-size: 16px;
    font-weight: 500;
    &:hover {
      cursor: pointer;
    }
    &--gradient {
      background: linear-gradient(269.95deg, #d680f1 0.02%, #765beb 97.45%);
      color: #fff;
    }
    &--with-shadow {
      box-shadow: 0px 5px 14px rgba(244, 105, 76, 0.25);
    }

    &--dark {
      background: linear-gradient(269.95deg, #d680f1 0.02%, #765beb 97.45%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      border-radius: 100px;
      position: relative;
      margin: 3px;
      &::before {
        position: absolute;
        content: '';
        background: linear-gradient(269.95deg, #d680f1 0.02%, #765beb 97.45%);
        top: -3px;
        bottom: -3px;
        left: -3px;
        right: -3px;
        z-index: -2;
        border-radius: 100px;
      }
      &::after {
        position: absolute;
        content: '';
        background-color: rgb(39, 42, 51);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
        border-radius: 100px;
      }
    }
  }
  &__note {
    font-family: 'Poppins';
    font-size: 14px;
    color: #fff;
    opacity: 0.5;
    margin-top: 15px;
  }
  &__checkbox {
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding-left: 25px;
    & input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
      &:checked ~ .authorization-modal__checkmark::after {
        display: block;
      }
    }
    &:hover input ~ .authorization-modal__checkmark {
      background-color: #ccc;
    }
    &:hover input:checked ~ .authorization-modal__checkmark {
      background-color: #ccc;
    }
    & input:checked ~ .authorization-modal__checkmark {
      background-color: #fff;
    }
    & .authorization-modal__checkmark::after {
      left: 5px;
      top: 1px;
      width: 3px;
      height: 8px;
      border: solid #7b5dd9;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
    }
  }
  &__checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 15px;
    width: 15px;
    background-color: #eee;
    border-radius: 3px;
    &::after {
      content: '';
      position: absolute;
      display: none;
    }
  }
  &__notification {
    text-align: left;
    margin: 32px 0;
  }
  &__img {
    display: inline-block;
  }
}
