@import '../../scss/colors';
@import '../../scss/fonts';

.suggest-cause {
  background-color: $forcards;
  border-radius: 10px;
  width: 100%;
  margin-bottom: 45px;
  &__nav {
    background: $forcards;
    border: 1px solid #414348;
    border-radius: 10px 10px 0 0;
    padding: 27px 32px;
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    &-icon {
      border: 1px solid rgba(255, 255, 255, 0.5);
      border-radius: 9px;
      margin-right: 18px;
      img {
        padding: 10px;
        display: inline-block;
        vertical-align: middle;
      }
    }
    &-title {
      font-size: 24px;
      line-height: 28px;
      font-weight: bold;
      color: #fff;
    }
  }
  &__content {
    display: flex;
    justify-content: center;
  }
}
.suggest-form-page {
  width: 100%;
  &__wrapper {
    max-width: 868px;
    background-color: #fff;
    margin: 0 auto;
    position: relative;
    z-index: 0;
    border-radius: 25px;
    margin-bottom: 35px;
  }
  &__content {
    padding: 35px 80px;
    margin: 0 auto;
    &-tab {
      font-weight: 500;
      font-size: 24px;
      line-height: 36px;
      text-align: center;
      margin-bottom: 50px;
    }
    &-inputs {
      width: 100%;
      label {
        background: #f5f5f5;
        border: 1px solid #e6e6e6;
        border-radius: 10px;
        margin-bottom: 12px;
        padding: 18px 25px;
        width: 100%;
        display: block;
        &.suggest-form-page__content-inputs__required {
          border: 1px solid red;
        }
      }
      input {
        font-family: 'Poppins', 'Roboto', 'Arial', sans-serif;
        outline: none;
        font-size: $mniFZ;
        line-height: $mniLH;
        width: 100%;
        border: none;
        background-color: transparent;
        font-weight: 500;
      }
    }
  }
  &__send-btn {
    cursor: pointer;
    width: 256px;
    margin: 0 auto;
    border: none;
    border-radius: 100px;
    color: #fff;
    background: $purpleGrad;
    display: block;
    font-size: $btnFZ;
    line-height: $btnLH;
    font-weight: 600;
    padding: 15px 0;
    margin-bottom: 32px;
  }
}
.suggest__modal {
  &-close {
    width: 23px;
    height: 23px;
    display: block;
    margin-right: 0;
    margin-left: auto;
    background-image: url('../../assets/images/close-icon.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  &-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 50px;
    color: #fff;
    padding-bottom: 24px;
  }
  &-img {
    width: 166px;
    background-image: url('../../assets/images/suggest-modal-img.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    height: 80px;
    margin-bottom: 24px;
  }
  &-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: $text;
    max-width: 258px;
    padding-bottom: 24px;
  }
}
